import React from 'react';
import {BrowserRouter as Router, Redirect, Route} from 'react-router-dom';
import NavBar from './pages/NavBar/NavBar';
import Home from './pages/Home/Home.js';
import AboutUs from './pages/AboutUs/AboutUs';
import Schedule from './pages/2022/Schedule/Schedule';
import Workshops from './pages/2022/Workshops/Workshops';
import Conferences from './pages/2022/Conferences/Conferences';
import Hackathon from './pages/2022/Hackathon/Hackathon';
import Winners from './pages/2022/Winners/Winners';
import OurTeam from './pages/OurTeam/OurTeam';
import Partners from './pages/Partners/Partners';
import Footer from './pages/Footer/Footer';
import './App.css';
import './Media.css';

import '@fontsource/lexend-mega';
import '@fontsource/nunito-sans';

class App extends React.Component {
    state = {
        language: 'en'
    };

    changeLanguageHandler = (language) => {
        this.setState({language: language});
    }

    render() {
        return (
            <Router id={'App'}>
                <NavBar language={this.state.language}/>

                <Route exact path={'/'}>
                    <Redirect to={'/home'}/>
                </Route>
                <Route path={'/home'}> <Home language={this.state.language}/> </Route>
                <Route path={'/about-us'}> <AboutUs language={this.state.language}/> </Route>

                <Route exact path={'/schedule'}> <Schedule language={this.state.language}/> </Route>
                <Route path={'/workshops'}> <Workshops language={this.state.language}/> </Route>
                <Route path={'/conferences'}> <Conferences language={this.state.language}/> </Route>
                <Route path={'/hackathon'}> <Hackathon language={this.state.language}/> </Route>
                <Route path={'/winners'}> <Winners language={this.state.language}/> </Route>

                <Route path={'/our-team'}> <OurTeam language={this.state.language}/> </Route>
                <Route path={'/partners'}> <Partners language={this.state.language}/> </Route>

                <Footer/>
            </Router>
        )
    }
}

export default App;
