import React from 'react';
import {getTranslation} from '../../text/translate';
import './AboutUs.css';

const ListItem = (props) => {
    return (
        <li>{props.value}</li>
    )
}

const AboutUs = (props) => {
    return (
        <div id={'about-us'} className={'page'}>
            <div className={'box'}>
                <h2 className={'title'}>{getTranslation(props.language, 'about', 'title')}</h2>
                <ul>
                    {getTranslation(props.language, 'about', 'items').map((item, index) =>
                        <ListItem key={index} value={item}/>
                    )}
                </ul>
            </div>
        </div>
    )
}

export default AboutUs;
